:root {
    scrollbar-width: none;
    -ms-overflow-style: none;
    --dashboard_width: 20%;
    --c_dashboard : #fcfaf8;
    --c_background: #ffffff;
    --c_linebreaker: #eeeeee;
    --c_datealert: #dd726a;
    --c_icons: rgb(220, 75, 63);
    --font-family-regular: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Apple Color Emoji", Helvetica, Arial, sans-serif, "Segoe UI Emoji", "Segoe UI Symbol";
}

:root::-webkit-scrollbar{
    display: none;
  }

* {
    font-family: var(--font-family-regular);  
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    font-size: 14px;
}

body {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100vw;
    color: #dc4b3f;
    background: linear-gradient(to right, #fafcfe 0%, #FFFFFF 50%, #fafcfe 100%);

    .main-content {
        width: 75vw;
        height: 75vh;
        border-radius: 15px;
        background-color: #dc4b3f;
        box-shadow: rgba(200, 200, 200, 0.5) 0px 0px 10px 2px, inset rgba(240, 240, 240, 0.5) 0px 0px 10px 2px;
        background-color: var(--c_background);
        display: grid;
        grid-template-columns: var(--dashboard_width) auto;

        .dashboard {
            border-radius: 15px;
            background-color: var(--c_dashboard);

            .header {
                margin-top: 10px;
                height: 5vh;
                padding: 15px;
                display: flex;
                justify-content: space-between;
                align-items: center;

                .profile {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    gap: 8px;

                    button {
                        color: #dc4b3f;
                        display: flex;
                        align-items: center;
                        text-align: center;
                    }

                    img {
                        border: 2px solid #dc4b3f;
                        height: 35px;
                        border-radius: 50%;
                    }
                }

                button {
                    border: none;
                    background-color: var(--c_dashboard);
                    cursor: pointer;
                }

            }

            ul {
                margin-top: 5vh;
                list-style: none;
                padding-left: 0px;

                li {
                    display: flex;
                    align-items: center;
                    gap: 2px;
                    margin: 10px 15px;
                }
            }


        }

        .content {
            border-radius: 15px;
            background-color: var(--c_background);

            h1 {
                margin: 50px;
                font-size: 1.8rem;
            }

            ul:hover,
            ul:active,
            ul:focus {
              overflow-y: scroll;
              scrollbar-width: thin;
            }

            ul {
                height: 500px;
                overflow-y: hidden;
                list-style: none;
                padding-left: 0px;
                margin-left: 50px;

                li {
                    margin-bottom: 15px;

                    /* .task label {
                        color: black;
                        margin-left: 2px;
                        font-size: 1.2rem;
                    } */

                    .date {
                        margin-left: 20px;
                        display: flex; 
                        align-items: center;
                        gap: 5px;

                        p {
                            font-size: 0.9rem;
                        }
                    }
                }

            }
        
        }
        
    }
}


.checkbox-wrapper-12 {
    display: flex;
    align-items: center;
    gap: 5px;
    text-align: center;
    position: relative;

    p {
      margin: 0px;
      font-size: 1.2rem;
    }

    p.hidden {
        color: grey;
        text-decoration: line-through;
    }
  }
.checkbox-wrapper-12 > svg {
position: absolute;
top: -130%;
left: -170%;
width: 110px;
pointer-events: none;
}
.checkbox-wrapper-12 * {
box-sizing: border-box;
}
.checkbox-wrapper-12 input[type="checkbox"] {
-webkit-appearance: none;
-moz-appearance: none;
appearance: none;
