cursor: pointer;
margin: 0;
}
.checkbox-wrapper-12 input[type="checkbox"]:focus {
outline: 0;
}
.checkbox-wrapper-12 .cbx {
width: 16px;
height: 16px;
top: calc(50vh - 12px);
left: calc(50vw - 12px);
}
.checkbox-wrapper-12 .cbx input {
position: absolute;
top: 1px;
left: 0;
width: 16px;
height: 16px;
border: 2px solid #bfbfc0;
border-radius: 50%;
}
.checkbox-wrapper-12 .cbx label {
width: 16px;
height: 16px;
background: none;
border-radius: 50%;
position: absolute;
top: 1px;
left: 0;
-webkit-filter: url("#goo-12");
filter: url("#goo-12");
transform: trasnlate3d(0, 0, 0);
pointer-events: none;
}
.checkbox-wrapper-12 .cbx svg {
position: absolute;
top: 5px;
left: 4px;
z-index: 1;
pointer-events: none;
